import styled from "@emotion/styled";
import { AxiosError } from "axios";
import { useMemo, useState } from "react";
import { useNavigate } from "react-router-dom";

import {
  type ProductDetailRequestParams,
  useGetProductDetail,
} from "@/api/hooks/useGetProductDetail";
import { useAddWish } from "@/api/hooks/useWish";
import { Button } from "@/components/common/Button";
import { Spacing } from "@/components/common/layouts/Spacing";
import { useAuth } from "@/provider/Auth";
import { getDynamicPath, RouterPath } from "@/routes/path";
import { orderHistorySessionStorage } from "@/utils/storage";

import { CountOptionItem } from "./OptionItem/CountOptionItem";

type Props = ProductDetailRequestParams;

export const OptionSection = ({ productId }: Props) => {
  const { data: detail } = useGetProductDetail({ productId });
  const options = detail.options;

  const [currentOption, setCurrentOption] = useState(options[0].id ?? 0);
  const [countAsString, setCountAsString] = useState("1");
  const totalPrice = useMemo(() => {
    return detail.price * Number(countAsString);
  }, [detail, countAsString]);

  const navigate = useNavigate();
  const authInfo = useAuth();

  const { mutate: addWish } = useAddWish(productId);

  const handleClick = () => {
    if (!authInfo) {
      const isConfirm = window.confirm(
        "  .\n  ?",
      );

      if (!isConfirm) return;
      return navigate(getDynamicPath.login());
    }

    orderHistorySessionStorage.set({
      productId: parseInt(productId),
      optionId: currentOption,
      count: parseInt(countAsString),
    });

    navigate(RouterPath.order);
  };

  const handleWish = () => {
    if (!authInfo) {
      const isConfirm = window.confirm(
        "  .\n  ?",
      );

      if (!isConfirm) return;
      return navigate(getDynamicPath.login());
    }

    const handleError = (error: Error) => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.error || error.message;
        alert(`  : ${errorMessage}`);
      } else {
        alert(`  : ${error.message}`);
      }
    };

    addWish(
      { productId: parseInt(productId) },
      {
        onSuccess: () => {
          alert("  .");
        },
        onError: (error) => {
          handleError(error);
        },
      },
    );
  };

  const handleOption = (id: number) => () => {
    setCurrentOption(id);
    setCountAsString("1");
    console.log(currentOption, countAsString, totalPrice);
  };

  return (
    <Wrapper>
      <TopWrapper>
        {options.map((option) =>
          option.id === currentOption ? (
            <CountOptionItem
              key={option.id}
              name={option.name}
              value={countAsString}
              onChange={setCountAsString}
              isSelect={true}
              maxValues={option.quantity}
            />
          ) : (
            <CountOptionItem
              key={option.id}
              name={option.name}
              value={countAsString}
              onChange={setCountAsString}
              onClick={handleOption(option.id)}
              isSelect={false}
            />
          ),
        )}
      </TopWrapper>
      <BottomWrapper>
        <PricingWrapper>
             <span>{totalPrice}</span>
        </PricingWrapper>
        <Button theme="black" size="large" onClick={handleClick}>
           
        </Button>
        <Spacing
          height={{
            initial: 10,
            sm: 15,
          }}
        />
        <Button theme="lightGray" size="large" onClick={handleWish}>
           
        </Button>
      </BottomWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  padding: 30px 12px 30px 30px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const TopWrapper = styled.div`
  justify-content: flex-start;
`;

const BottomWrapper = styled.div`
  padding: 12px 0 0;
`;

const PricingWrapper = styled.div`
  margin-bottom: 20px;
  padding: 18px 20px;
  border-radius: 4px;
  background-color: #f5f5f5;
  display: flex;
  justify-content: space-between;

  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  color: #111;

  & span {
    font-size: 20px;
    letter-spacing: -0.02em;
  }
`;
