import { Divider } from "@chakra-ui/react";
import styled from "@emotion/styled";

import { useGetProductDetail } from "@/api/hooks/useGetProductDetail";
import { Button } from "@/components/common/Button";
import { Spacing } from "@/components/common/layouts/Spacing";
import type { OrderHistory } from "@/types";

import { HeadingText } from "../Common/HeadingText";
import { LabelText } from "../Common/LabelText";
import { CashReceiptFields } from "../Fields/CashReceiptFields";
import { PointFields } from "../Fields/PointFields";

type Props = {
  orderHistory: OrderHistory;
};
export const OrderFormOrderInfo = ({ orderHistory }: Props) => {
  const { productId: id, count } = orderHistory;

  const { data: detail } = useGetProductDetail({ productId: id.toString() });
  const totalPrice = detail.price * count;

  return (
    <Wrapper>
      <Title>
        <HeadingText> </HeadingText>
      </Title>
      <Divider color="#ededed" />
      <CashReceiptFields />
      <Divider color="#ededed" />
      <PointFields />
      <Divider color="#ededed" />
      <ItemWrapper>
        <LabelText> </LabelText>
        <HeadingText>{totalPrice}</HeadingText>
      </ItemWrapper>
      <Divider color="#ededed" />
      <Spacing height={32} />
      <Button type="submit">{totalPrice} </Button>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  border-left: 1px solid #ededed;
  border-right: 1px solid #ededed;
  padding: 16px;
`;

const Title = styled.h6`
  padding: 24px 0 20px;
`;

const ItemWrapper = styled.div`
  padding: 16px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
